import React, { Component  } from 'react';
import { StaticQuery, graphql } from "gatsby";
import Navbar from '../components/navbar';
import Contact from '../components/contact';
import Footer from '../components/footer';
import Download from "../images/download.webp";


export default function Terms () {
  
  return (
    <StaticQuery
      query = {graphql`
      query Terms {
          allWpPage(filter: {slug: {eq: "terms"}}) {
            nodes {
              content
            }
          }
        }
      `}
      render = { data => (
        <><Navbar /><section
          id="post-198"
          class="post-198 page type-page status-publish hentry"
        >
          <header class="entry-header text-center" style={{ marginTop: "100px" }}>
            <h1 class="entry-title">Terms Of Use</h1>
          </header>
          <div class="entry-content">
            <div class="container">
              <div class="row">
                <div class="col-sm-8 m-auto" style={{ padding: "0 35px" }}>
                  <p>
                    MAHA RERA Act has come into effect from 1st May 2017. The
                    content of the website may not be fully compliant with RERA as
                    of date. We are in process of revising further details required
                    as per RERA if pending any.
                  </p>
                  <p>
                    We all possible measures to avoid any misrepresentation but make
                    no warranties as to the content and information accuracy. While
                    reasonable efforts are being made to ensure the authenticity and
                    completeness of the Information displayed here and the same is
                    updated at regular intervals there is a possibility that the
                    information displayed may not be current and incomplete.
                  </p>
                  <p>
                    Please make sure you verify all the details from the actual site
                    office or from the RERA website before making any decision in
                    relation to the purchase of the properties.
                  </p>
                  <p>
                    We accept no liability for the content of this website content,
                    or for the consequences of any actions taken on the basis of
                    this website or content. Property Prices, location map image,
                    master plan image, opinion/suggestions provided in this mail are
                    subject to change without notice and shall in no way make us
                    responsible for any loss/damages caused to the users/members.
                  </p>
                </div>
              </div>
            </div>
          </div>
        </section><Contact/><Footer /></>

      )}
    />
  )
}